import { Meta, StoryObj } from '@storybook/react';
import { Dropdown } from '../../lib/features/dropdowns/Dropdown';
import { BaseIcon } from '../../lib/features/icons/BaseIcon';
import { MenuItem } from '../../feature/';
import { PopUp } from '../../feature/floating/pop-up.component';

const meta: Meta = {
  title: 'Dropdown/DropdownComponent',
  component: Dropdown,
  subcomponents: { BaseIcon, MenuItem, PopUp },
  decorators: [
    (Story) => (
      <div style={{ width: '100px' }}>
        <Story />
      </div>
    ),
  ],
  argTypes: {
    theme: {
      options: ['light-theme', 'dark-theme'],
      control: { type: 'radio' },
      table: { defaultValue: { summary: 'light-theme' } },
    },
    disabled: {
      table: { defaultValue: { summary: 'false' } },
    },
    collapsePlacement: {
      options: ['left', 'right'],
      control: { type: 'radio' },
      table: { defaultValue: { summary: 'right' } },
    },
    activeState: {
      description:
        'Output property returning true if the dropdown is open and false if the dropdown is closed.',
    },
    showIcon: {
      table: { defaultValue: { summary: 'false' } },
    },
    showText: {
      table: { defaultValue: { summary: 'true' } },
    },
  },
  render: (args) => (
    <Dropdown
      {...args}
      label="Label"
      updateLabel={(label: string) => args.label = label}
    >
      <BaseIcon prevIcon size="base" name="add" />
      <MenuItem
        options
        onClick={() => args.updateLabel('Option1')}
        isActive={args.label === 'Option1'}
        label="Option1"
        showIcon={true}
        showText={true}
        showCollapseIcon={false}
        theme={args.theme}
      />
      <MenuItem
        options
        onClick={() => args.updateLabel('Option2')}
        label="Option2"
        isActive={args.label === 'Option2'}
        showIcon={true}
        showText={true}
        showCollapseIcon={false}
        theme={args.theme}
      />
      <MenuItem
        options
        onClick={() => args.updateLabel('Option3')}
        label="Option3"
        isActive={args.label === 'Option3'}
        showIcon={true}
        showText={true}
        showCollapseIcon={false}
        theme={args.theme}
      />
      <MenuItem
        options
        onClick={() => args.updateLabel('Option4')}
        label="Option4"
        isActive={args.label === 'Option4'}
        showIcon={true}
        showText={true}
        showCollapseIcon={false}
        theme={args.theme}
      />
      <MenuItem
        options
        onClick={() => args.updateLabel('Option5')}
        label="Option5"
        isActive={args.label === 'Option5'}
        showIcon={true}
        showText={true}
        showCollapseIcon={false}
        theme={args.theme}
      />
    </Dropdown>
  ),
  tags: ['autodocs'],
};

export default meta;

type Story = Story<typeof meta>;

export const LightTheme: Story = {
  parameters: {
    backgrounds: { default: 'Light Theme' },
    docs: {
      description: {
        story: '',
      },
    },
  },
  args: {
    label: 'Label',
    theme: 'light-theme',
    collapsePlacement: 'right',
    showIcon: true,
    showText: true,
    disabled: false,
  },
};

export const DarkTheme: Story = {
  parameters: {
    backgrounds: { default: 'Dark Theme' },
  },
  args: {
    label: 'Label',
    theme: 'dark-theme',
    showIcon: true,
    showText: true,
    collapsePlacement: 'right',
    disabled: false,
  },
};
